	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Dip in the Seeve","date":"05 Jul 2006","slug":"/2006-07-05-dip-in-the-seeve/","tags":"Y2006"},"html":"<p><a href=\"http://www.flickr.com/photos/papascott/181730517/\" title=\"Photo Sharing\"><img src=\"https://static.flickr.com/65/181730517_1c4e127aff.jpg\" width=\"500\" height=\"375\" alt=\"IMG 1547\" /></a></p>\n<p><!-- ckey=\"5C548C12\" -->We have temperatures well over 32째C (90째F), so yesterday afternoon Christopher cooled off in the Seeve, the creek that runs near our house. It used to be a popular starting point for canoes, but the banks have eroded and the county has now banned all boats. They're currently building up the bank, so Christopher had to nearly crawl over a backhoe to get in the water</p>\n<p>There's a slideshow <a href=\"http://www.flickr.com/photos/papascott/sets/72157594187886703/show/\">here</a> for grandparents and other Christopher fans.</p>"},"next":{"fields":{"title":"Christopher Wins","date":"04 Jul 2006","slug":"/2006-07-04-christopher-wins/"}},"previous":{"fields":{"title":"Germany 0 Italy 2","date":"05 Jul 2006","slug":"/2006-07-05-germany-0-italy-2/"}}},{"node":{"fields":{"title":"Christopher Wins","date":"04 Jul 2006","slug":"/2006-07-04-christopher-wins/","tags":"Y2006"},"html":"<p><a href=\"http://fifaworldcup.yahoo.com/06/en/060704/1/8km5.html\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2006/07/italys_goal_against_germany_2006.jpg\" alt=\"Itagoal\" /></a></p>\n<p>And he doesn't even know that <a href=\"http://www.papascott.de/archives/2006/07/02/italia/\">his team</a> won yet. He fell asleep at halftime.</p>"},"next":{"fields":{"title":"Trick Ankle","date":"02 Jul 2006","slug":"/2006-07-02-trick-ankle/"}},"previous":{"fields":{"title":"Dip in the Seeve","date":"05 Jul 2006","slug":"/2006-07-05-dip-in-the-seeve/"}}},{"node":{"fields":{"title":"Trick Ankle","date":"02 Jul 2006","slug":"/2006-07-02-trick-ankle/","tags":"Y2006"},"html":"<p>After running 5K in the Buchholzer Stadtlauf last year, MamaMaus has been looking forward to running 10K this year. The big race was today, her firm was a main sponsor and she started together with a dozen colleagues. The weather was warm, but a a breeze kept things from getting too hot.</p>\n<p><a href=\"http://www.flickr.com/photos/papascott/179975788/\" title=\"Photo Sharing\"><img src=\"https://static.flickr.com/75/179975788_90da32fd62.jpg\" width=\"500\" height=\"375\" alt=\"IMG 1535\" /></a></p>\n<p>Alas, she never finished. About 500m into the run she stepped on a plastic bottle that someone had dropped, her ankle gave way, and fell in agony to the ground. The medics took her away in an ambulance, and her race was over. She has torn ligaments in the <a href=\"http://www.papascott.de/archives/2003/01/18/injured-reserve/\">same ankle</a> as in 2003, and will be limping for 4 weeks. Good thing we didn't toss out the crutches.</p>\n<p>And she'll miss the World Cup final in Berlin next weekend.</p>"},"next":{"fields":{"title":"Italia","date":"02 Jul 2006","slug":"/2006-07-02-italia/"}},"previous":{"fields":{"title":"Christopher Wins","date":"04 Jul 2006","slug":"/2006-07-04-christopher-wins/"}}}],"pathPrefix":"","first":false,"last":false,"index":210,"pageCount":1101}}

